@import './theme';
@import './mixins';
@import './var.scss';
.dao-rights-burn-box {
  .dao-rights-burn {
    display: flex;
    align-items: center;
    padding: 40px;
    min-height: 300px;
    background: url('../../assets/images/dao/rights/bg_rights_burn.png') no-repeat;
    background-size: 100% 100%;
    font-family: PingFangSC, PingFang SC;
    background-color: rgba(231, 222, 255, 0.03);
    backdrop-filter: blur(12px);
    border-radius: 10px;

    @include lightThemeOnly {
      border-radius: 0;
      background-image: url('../../assets/images/dao/rights/bg_rights_burn_light.png');
    }

    .dao-rights-burn-l {
      position: relative;
      flex: 1;
      width: 0;
      &::after {
        content: ' ';
        position: absolute;
        right: 0;
        top: 16px;
        width: 1px;
        height: calc(100% - 32px);
        background: $color-border-primary;
        border-radius: 1px;
      }

      .dao-rights-burn-title {
        font-weight: 400;
        font-size: 20px;
        color: $color-text-default;
        line-height: 28px;
        font-style: normal;
      }
      .dao-rights-burn-num {
        font-family: AlibabaPuHuiTiH;
        font-size: 60px;
        color: $color-text-default;
        line-height: 82px;
        font-style: normal;
      }

      .dao-rights-burn-worth {
        font-weight: 400;
        font-size: 16px;
        color: $color-text-default-7;
        line-height: 22px;
        font-style: normal;
      }

      .dao-rights-burn-btn {
        margin-top: 40px;
        .dao-jump-btn {
          padding: 14px 40px 14px 40px;
          background: transparent;
          backdrop-filter: initial;
          &:hover {
            background: linear-gradient(315deg, #7700ff 0%, #8c51ff 100%);
          }
        }
      }
    }
  }

  .dao-rights-burn-r {
    padding-left: 60px;
    width: 460px;
    .dao-rights-burn-desc {
      font-weight: 600;
      font-size: 36px;
      color: $color-text-default;
      line-height: 50px;
    }

    .dao-rights-burn-sub {
      margin-top: 20px;
      font-weight: 400;
      font-size: 16px;
      color: $color-text-default-7;
      line-height: 28px;
      text-align: left;
      font-style: normal;
    }
  }
}

@include mobile-only {
  .dao-rights-burn-box {
    .dao-rights-burn {
      display: block;
      padding: 14px 10px;
      min-height: 120px;

      .dao-rights-burn-l {
        position: relative;
        width: 100%;
        text-align: center;
        &::after {
          display: none;
        }

        .dao-rights-burn-title {
          font-size: 14px;
          line-height: 20px;
        }
        .dao-rights-burn-num {
          margin-top: 10px;
          font-size: 26px;
          line-height: 35px;
        }

        .dao-rights-burn-worth {
          margin-top: 8px;
          font-size: 12px;
          line-height: 17px;
        }

        .dao-rights-burn-btn {
          display: none;
        }
      }
      .dao-rights-burn-r {
        display: none;
      }
    }

    .dao-rights-burn-r {
      padding-left: 0;
      margin-bottom: 20px;
      width: 100%;
      .dao-rights-burn-desc {
        font-size: 20px;
        line-height: 28px;
      }

      .dao-rights-burn-sub {
        margin-top: 14px;
        font-size: 14px;
        line-height: 24px;
      }
    }

    .dao-jump-btn {
      display: block;
      margin-top: 20px;
    }
  }
}
