@import './theme';
@import './mixins';
@import './var.scss';
.dao-rights {
  // background: url('../../assets/images/dao/rights/banner.png') no-repeat;
  // background-size: 100% 590px;
  // @include lightThemeOnly {
  //   background-image: url('../../assets/images/dao/rights/banner_light.png');
  // }

  .dao-rights-layout {
    width: 1200px;
    margin: 0 auto;
  }

  .dao-rights-wrap {
    position: relative;
    width: 100%;
  }

  .dao-rights-mobile {
    display: none !important;
  }

  // &.dao-rights-notoast {
  //   .dao-right-banner {
  //     margin-top: -72px;
  //   }
  // }

  .expanded-row {
    color: #c1a0ff;
    display: inline-block;
    line-height: 24px;
    @include lightThemeOnly {
      color: #5a14e1;
    }
  }

  // Banner
  .dao-right-banner {
    // z-index: -1;
    position: relative;
    text-align: center;
    margin-top: -72px;
    img {
      display: block;
      width: 100%;
    }

    .dao-rights-dark {
      display: block !important;
      @include lightThemeOnly {
        display: none !important;
      }
    }

    .dao-rights-light {
      display: none !important;
      @include lightThemeOnly {
        display: block !important;
      }
    }

    .dao-rights-banner-text {
      position: absolute;
      left: 0;
      top: 47%;
      transform: translateY(-50%);
      width: 100%;
      line-height: 90px;
      font-size: 80px;
      text-align: center;
      span {
        background: linear-gradient(136deg, #b144ff 0%, #7e58ff 100%);
        -webkit-background-clip: text;
        background-clip: text;
        color: transparent;
      }
    }
  }

  // Platform
  .dao-rights-layout-platform {
    margin-top: 80px;
  }

  .dao-rights-layout-platform-top {
    position: relative;
    padding-left: 622px;
    .dao-rights-layout-platform-l {
      // z-index: -2;
      position: absolute;
      right: 353px;
      top: -178px;
      width: 1140px;
      img {
        display: block;
        width: 100%;
      }
    }
    .dao-rights-layout-platform-title {
      font-family: PingFangSC, PingFang SC;
      font-weight: 600;
      font-size: 60px;
      color: $color-text-default;
      line-height: 80px;
    }
    .dao-rights-layout-platform-sub {
      font-family: PingFangSC, PingFang SC;
      font-weight: 600;
      font-size: 40px;
      color: $color-text-default;
      line-height: 60px;
    }
    .dao-rights-layout-platform-desc {
      position: relative;
      margin-top: 20px;
      font-family: PingFangSC, PingFang SC;
      font-weight: 400;
      font-size: 16px;
      color: $color-text-default-7;
      line-height: 28px;
      .ant-typography {
        color: inherit;
      }
    }
    .dao-rights-layout-platform-btn {
      display: flex;
      justify-content: flex-end;
      margin-top: 30px;
      padding-right: 20px;
      .dao-jump-btn {
        padding-left: 40px;
        padding-right: 40px;
      }
    }
  }

  .dao-rights-layout-platform-btm {
    z-index: 2;
    position: relative;
    display: flex;
    margin: 120px 0 100px;
    .dao-rights-layout-platform-num {
      display: flex;
      flex: 1;
      align-items: center;
      margin-right: 35%;
      height: 200px;
      background: #291a42;
      @include lightThemeOnly {
        background: rgba(255, 255, 255, 0.5);
        border: 2px solid #ffffff;
        backdrop-filter: blur(10px);
      }
      border-radius: 0px 20px 20px 0px;
      .dao-rights-layout-platform-num-item {
        position: relative;
        padding: 9px 20px 3px;
        height: 146px;
        &:first-child {
          flex: 1;
          width: 0;
        }
        &:nth-child(2) {
          min-width: 600px;
          text-align: center;
        }
        &:last-child {
          min-width: 300px;
          text-align: center;
          &::before {
            content: '';
            position: absolute;
            left: 0;
            top: 0;
            width: 1px;
            height: 100%;
            background: $color-text-default;
            border-radius: 1px;
            opacity: 0.2;
          }
        }
        .dao-rights-layout-platform-num-title {
          span {
            font-size: 74px;
            background: linear-gradient(315deg, #502fff 0%, #ee51ff 100%);
            -webkit-background-clip: text;
            background-clip: text;
            color: transparent;
            font-family: AlibabaPuHuiTiH;
          }
        }
        .dao-rights-layout-platform-num-desc {
          margin-top: 30px;
          font-family: Helvetica;
          font-size: 16px;
          color: $color-text-default-3;
          line-height: 19px;
        }
      }
    }
  }

  .dao-jump-btn {
    position: relative;
    display: inline-block;
    padding: 14px 16px;
    border-radius: 5px;
    overflow: hidden;
    cursor: pointer;
    backdrop-filter: blur(3px);
    text-align: center;

    @include lightThemeOnly {
      background: #f7f6f8;
      backdrop-filter: blur(8px);
    }
    img {
      position: absolute;
      left: 0;
      top: 0;
      display: block;
      width: 100%;
      height: 100%;
    }

    span {
      z-index: 3;
      position: relative;
      background: linear-gradient(315deg, #8e59ff 0%, #ba5bff 100%);
      -webkit-background-clip: text;
      background-clip: text;
      color: transparent;
    }

    &:hover {
      background: linear-gradient(315deg, #7700ff 0%, #8c51ff 100%);
      img {
        display: none;
      }
      @include lightThemeOnly {
        background: linear-gradient(315deg, #7700ff 0%, #8c51ff 100%);
      }
      span {
        color: #fff;
      }
    }
  }

  // Vesun
  .dao-rights-vesun-wrap {
    .dao-rights-vesun-header {
      display: flex;
      align-items: center;
      .dao-rights-vesun-header-l {
        flex: 1;
        width: 0;
        .dao-rights-vesun-header-title {
          font-family: PingFangSC, PingFang SC;
          font-weight: 600;
          font-size: 60px;
          color: $color-text-default;
          line-height: 80px;
        }
        .dao-rights-vesun-header-sub {
          font-family: PingFangSC, PingFang SC;
          font-weight: 600;
          font-size: 40px;
          color: $color-text-default;
          line-height: 80px;
        }
      }

      .dao-rights-vesun-header-r {
        position: relative;
        // margin-left: 80px;
        width: 510px;
        font-family: PingFangSC, PingFang SC;
        font-weight: 400;
        font-size: 20px;
        color: $color-text-default-7;
        line-height: 32px;
        img {
          position: absolute;
          display: block;
          top: -34px;
          left: -50px;
          width: 100px;
          height: 100px;
        }
      }
    }

    .dao-rights-vesun-list {
      display: flex;
      margin-top: 40px;
      .dao-rights-vesun-item {
        position: relative;
        flex: 1;
        margin-right: 40px;
        padding: 24px 30px 51px 30px;
        background: rgba(231, 222, 255, 0.03);
        border-radius: 10px;
        border: 1px solid $color-border-primary;
        backdrop-filter: blur(12px);
        cursor: pointer;
        @include lightThemeOnly {
          background: rgba(255, 255, 255, 0.5);
          border-color: #fff;
        }
        &:hover {
          background: rgba(231, 222, 255, 0.1);
          @include lightThemeOnly {
            background: rgba(255, 255, 255, 1);
          }
          .dao-jump-btn {
            background: linear-gradient(315deg, #7700ff 0%, #8c51ff 100%);
            img {
              display: none;
            }
            span {
              color: #fff;
            }
          }
          .dao-rights-vesun-icon {
            img {
              opacity: 1;
            }
          }
        }
        &:last-child {
          margin-right: 0;
        }

        .dao-rights-vesun-icon {
          width: 94px;
          height: 94px;
          img {
            display: block;
            width: 100%;
            height: 100%;
            opacity: 0.7;
          }
        }

        .dao-rights-vesun-title {
          margin: 18px 0 20px;
          font-family: PingFangSC-Regular, PingFangSC, PingFang SC;
          font-weight: 600;
          font-size: 24px;
          color: $color-text-default;
          line-height: 33px;
          font-style: normal;
        }

        .dao-rights-vesun-sub {
          font-family: PingFangSC-Regular, PingFangSC, PingFang SC;
          font-weight: 400;
          font-size: 16px;
          color: $color-text-default-7;
          line-height: 24px;
          font-style: normal;
        }

        .dao-rights-vesun-btn {
          position: absolute;
          left: 30px;
          bottom: 0;
          transform: translateY(50%);
        }
      }
    }
  }

  // Burn
  .dao-rights-burn-wrap {
    margin-top: 150px;
  }
}

@include min-desktop-only {
  .dao-rights {
    .dao-rights-layout {
      width: 100%;
      padding: 0 20px;
    }

    // Platform
    .dao-rights-layout-platform {
      margin-top: 40px;
    }

    .dao-rights-layout-platform-top {
      padding-left: 50%;
      .dao-rights-layout-platform-l {
        right: 405px;
        top: -75px;
        width: 80%;
      }
    }

    .dao-rights-layout-platform-btm {
      margin: 120px 0 100px;
    }
  }
}

@include mobile-only {
  .dao-rights {
    // background-size: 100% 195px;
    .dao-rights-layout {
      width: 100%;
      padding: 0;
      margin: 0 auto;
    }

    .dao-rights-wrap {
      // z-index: -1;
      position: relative;
      padding: 0 15px;
      margin-top: 0;
      width: 100%;
      margin-bottom: 20px;
      .dao-rights-banner-text {
        // margin-top: -3%;
      }
    }

    .dao-rights-mobile {
      display: block !important;
    }
    .dao-rights-pc {
      display: none !important;
    }

    // &.dao-rights-notoast {
    //   .dao-rights-wrap {
    //     margin-top: -46px;
    //     // .dao-rights-banner-text {
    //     //   margin-top: -15px;
    //     // }
    //   }
    //   .dao-right-banner {
    //     margin-top: 0;
    //   }
    // }

    // Banner
    .dao-right-banner {
      position: relative;
      margin: 0 -15px;
      text-align: center;

      .dao-rights-banner-text {
        line-height: 30px;
        font-size: 24px;
      }
    }

    // Platform
    .dao-rights-layout-platform {
      position: relative;
      margin-top: -10px;
    }

    .dao-rights-layout-platform-top {
      padding-left: 0;
      .dao-rights-layout-platform-l {
        position: relative;
        right: 0;
        top: 0;
        width: inherit;
        height: 300px;
        img {
          position: absolute;
          left: -104px;
          top: 0;
          display: block;
          width: 115%;
        }
      }
      .dao-rights-layout-platform-title {
        margin-top: -30px;
        font-size: 20px;
        line-height: 28px;
      }
      .dao-rights-layout-platform-sub {
        margin-top: 6px;
        font-size: 16px;
        line-height: 22px;
      }
      .dao-rights-layout-platform-desc {
        margin-top: 14px;
        font-size: 14px;
        line-height: 24px;
      }
      .dao-rights-layout-platform-btn {
        display: flex;
        justify-content: flex-start;
        margin-top: 20px;
        padding-right: 0;
        .dao-jump-btn {
          padding-left: 30px;
          padding-right: 30px;
        }
      }
    }

    .dao-rights-layout-platform-btm {
      position: absolute;
      right: -15px;
      top: 48px;
      display: block;
      margin: 0;
      .dao-rights-layout-platform-num {
        // display: block;
        flex-direction: column-reverse;
        align-items: end;
        margin-right: 0;
        height: initial;
        background: transparent;
        @include lightThemeOnly {
          background: transparent;
          border: 0;
          backdrop-filter: none;
        }
        .dao-rights-layout-platform-num-item {
          position: relative;
          margin: 20px 0 0;
          padding: 13px 20px;
          min-width: initial;
          width: initial;
          height: initial;
          // background: rgba(231, 222, 255, 0.1);
          background: rgba(41, 26, 66, 0.8);
          border: 1px solid rgba(255, 255, 255, 0.1);
          backdrop-filter: blur(10px);
          border-radius: 10px 0px 0px 10px;
          @include lightThemeOnly {
            background: rgba(255, 255, 255, 0.5);
            border-color: #fff;
            backdrop-filter: blur(6px);
          }
          &:first-child {
            display: none;
          }
          &:nth-child(2) {
            min-width: initial;
            text-align: center;
          }
          &:last-child {
            min-width: initial;
            text-align: center;
            margin: 0;
            &::before {
              display: none;
            }
          }
          .dao-rights-layout-platform-num-title {
            span {
              font-size: 36px;
            }
          }
          .dao-rights-layout-platform-num-desc {
            margin-top: 7px;
            font-size: 12px;
            line-height: 14px;
          }
        }
      }
    }

    .dao-jump-btn {
      padding: 10px 24px;
      overflow: hidden;
      backdrop-filter: blur(3px);
      background: linear-gradient(315deg, #7700ff 0%, #8c51ff 100%);
      img {
        display: none;
      }
      @include lightThemeOnly {
        background: linear-gradient(315deg, #7700ff 0%, #8c51ff 100%);
      }
      span {
        background: transparent;
        color: #ffffff;
      }

      &:hover {
        background: linear-gradient(315deg, #7700ff 0%, #8c51ff 100%);
        span {
          color: #fff;
        }
      }
    }

    // Vesun
    .dao-rights-vesun-wrap {
      margin-top: 40px;
      .dao-rights-vesun-header {
        display: block;
        .dao-rights-vesun-header-l {
          width: 100%;
          .dao-rights-vesun-header-title {
            font-size: 20px;
            line-height: 28px;
          }
          .dao-rights-vesun-header-sub {
            margin-top: 6px;
            font-size: 16px;
            line-height: 22px;
          }
        }

        .dao-rights-vesun-header-r {
          margin-left: 0;
          margin-top: 14px;
          width: 100%;
          font-size: 14px;
          line-height: 24px;
          img {
            position: absolute;
            display: block;
            top: -37px;
            left: auto;
            right: 12px;
            width: 80px;
            height: 80px;
          }
        }
      }

      .dao-rights-vesun-list {
        display: block;
        margin-top: 16px;
        .dao-rights-vesun-item {
          position: relative;
          flex: initial;
          margin-right: 0;
          margin-bottom: 30px;
          padding: 14px 10px 40px 10px;
          @include lightThemeOnly {
            background: rgba(255, 255, 255, 0.5);
          }
          &:hover {
            background: rgba(231, 222, 255, 0.03);
            @include lightThemeOnly {
              background: rgba(255, 255, 255, 0.5);
            }
          }
          &:last-child {
            margin-bottom: 0;
          }
          .dao-rights-vesun-icon {
            position: absolute;
            right: 20px;
            bottom: 20px;
            width: 40px;
            height: auto;
            img {
              display: block;
              width: 100%;
              height: 100%;
              opacity: 1 !important;
            }
          }

          .dao-rights-vesun-title {
            margin: 0 0 12px;
            font-size: 16px;
            line-height: 22px;
          }

          .dao-rights-vesun-sub {
            font-size: 12px;
            line-height: 20px;
          }

          .dao-rights-vesun-btn {
            position: absolute;
            left: 10px;
            bottom: 0;
            transform: translateY(50%);
          }
        }
      }
    }

    // Brun
    .dao-rights-burn-wrap {
      margin-top: 50px;
    }
  }
}
